import './App.css';

import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";

import Header from './components/Header/Header.js';
import Footer from './components/Footer/Footer.js';
import Sidebar from './components/Sidebar/Sidebar.js';
import PageContentLanding from './components/PageContentLanding/PageContentLanding.js';
import AdminRouteMap from "./components/AdminRouteMap/AdminRouteMap";
import AdminDriverMap from "./components/AdminDriverMap/AdminDriverMap";
import AdminTable from "./components/AdminTable/AdminTable";

import React, { useState } from "react";
import {routeData} from "./data/routeData";

function App() {

  const [depot, setDepot] = useState(routeData);
  const [count, setCount] = useState(0);

  return (
  <Router>
    <div className="App">    
      <Switch>
        <Route exact path="/">
          <Header />
          <div className="container">
            <PageContentLanding />
            <Sidebar />
          </div>
          <Footer />
        </Route>
        <Route path="/about">
        <Header />  
        <div className="container">
          <div className="main-column">
            <p>Lorem ipsum dolor.</p>
          </div>
          <Sidebar />
        </div>
        <Footer />
        </Route>
        <Route exact path="/admin/route-map" render={ () => <AdminRouteMap display="Route Map"/>} />
        <Route exact path="/admin/driver-map" render={ () => <AdminDriverMap display="Driver Map"/>} />
        <Route exact path="/admin/depots" render={ () => <AdminTable display="Depots" />} />
        <Route exact path="/admin/trucks" render={ () => <AdminTable display="Truck" />} />
        <Route exact path="/admin/appliances" render={ () => <AdminTable display="Appliance" />} />
      </Switch>
    </div>
    </Router>
  );
}

export default App;