import AdminHeader from "../AdminHeader/AdminHeader";
import Footer from "../Footer/Footer";
import mapImage from './routemap.png';
import './AdminRouteMap.css';


const AdminRouteMap = (props) => {
    return (
      <div>
        <AdminHeader />
        <h1>{props.display}</h1>
        <div className="img-wrap">
          <img src={mapImage} alt={props.display} />
        </div>
        <Footer />
      </div>
    );
}

export default AdminRouteMap;